import { ChangeEventHandler, useCallback, useState } from "react";
import { useMediaQuery } from "react-responsive";
import { validEthAddr } from "./eth";

export function useHover() {
  const [hover, setHover] = useState(false);
  const onMouseEnter = useCallback(() => setHover(true), [setHover]);
  const onMouseLeave = useCallback(() => setHover(false), [setHover]);
  return { hover, onMouseEnter, onMouseLeave };
}

export function useMobileQuery() {
  const isPortrait = useMediaQuery({ query: "(orientation: portrait)" });
  return { usingMobile: isPortrait };
}

export function useDialog(open?: boolean) {
  const [show, setShow] = useState(open);
  const openDialog = useCallback(() => setShow(true), [setShow]);
  const onCloseComplete = useCallback(() => setShow(false), [setShow]);
  return { isShown: show, openDialog, onCloseComplete };
}

export function useSearchEthereumAddress() {
  const [address, setAddress] = useState<string>(``);
  const handleChange = useCallback<ChangeEventHandler<HTMLInputElement>>(
    (e) => setAddress(e.target.value),
    [setAddress]
  );
  const cleanInput = useCallback(() => setAddress(``), [setAddress]);
  const valid = validEthAddr(address);
  return { address, valid, handleChange, cleanInput };
}
