import { Col, Row } from "react-flexbox-grid";
import { Pane } from "evergreen-ui";
import { ProjectCard } from "./Card";

export function Data(): JSX.Element {
  return (
    <Row center="xs">
      {[...new Array(1)].map((i, key) => (
        <Col lg={4} md={6} xs={11} key={key}>
          <Pane paddingX={8} paddingY={16}>
            <ProjectCard data={key.toString()} />
          </Pane>
        </Col>
      ))}
    </Row>
  );
}
