import { defaultTheme, ThemeProvider, mergeTheme, Pane } from "evergreen-ui";
import { Outlet } from "react-router-dom";
import { Navigation } from "./Layout/Navigation";
import { Data } from "./Projects";
import { ScrollButton } from "./Components/ScrollTopButton";
import { Grid } from "react-flexbox-grid";
import { HashRouter as Router, Route, Routes } from "react-router-dom";

const Projects = () => <Data />;

const AccountProjects = () => <Data />;

const ProjectPartitions = () => <>Project</>;

const ProjectDocuments = () => <>Project | Documents</>;

const ProjectUsers = () => <>Project | Roles</>;

const TransactionSend = () => <>Transaction | Send</>;

const Layout = () => {
  const theme = mergeTheme(defaultTheme, {
    components: {
      Button: {
        baseStyle: {
          borderRadius: 12,
        },
      },
      Input: {
        baseStyle: {
          borderRadius: 30,
        },
      },
      Card: {
        baseStyle: {
          boxShadow: `0 .2rem 1.6rem rgba(34,34,34,.15)`,
          borderRadius: `1.5rem`,
        },
      },
    },
  });

  return (
    <ThemeProvider value={theme}>
      <Pane className="wallet-bg" />
      <Grid>
        <Navigation />
        <Outlet />
      </Grid>
      <ScrollButton />
    </ThemeProvider>
  );
};

export function Wallet() {
  return (
    <Router>
      <Routes>
        <Route element={<Layout />}>
          <Route index element={<Projects />} />

          <Route path="account/:address">
            <Route path="projects" element={<AccountProjects />} />
          </Route>

          <Route path="project/:address">
            <Route path="partitions" element={<ProjectPartitions />} />
            <Route path="documents" element={<ProjectDocuments />} />
            <Route path="accounts" element={<ProjectUsers />} />
          </Route>

          <Route path="tx/:address/:data">
            <Route path="send" element={<TransactionSend />} />
          </Route>
        </Route>
      </Routes>
    </Router>
  );
}
