import { Col, Row } from "react-flexbox-grid";
import {
  Avatar,
  Card,
  EnhancerProps,
  Heading,
  IconButton,
  majorScale,
  minorScale,
  Pane,
  StarEmptyIcon,
  StarIcon,
} from "evergreen-ui";
import { Chart } from "./Chart";
import { createContext, useContext, useState } from "react";
import { useHover } from "utils/hooks";
import { Props } from "utils/props";

const Context = createContext<string>(``);

export function ProjectCard(props: Props<string>): JSX.Element {
  const { hover, ...handleHover } = useHover();

  return (
    <Context.Provider value={props.data}>
      <Card backgroundColor="white" cursor="pointer" {...handleHover}>
        <Pane padding={majorScale(5)}>
          <Row middle="xs" between="xs">
            <Col>
              <TokenName />
            </Col>
            <Col>
              <WatchTokenButton />
            </Col>
          </Row>
          <Pane marginBottom={minorScale(2)} />
          <Row middle="xs" between="xs">
            <Col>
              <TokenLogo />
            </Col>

            <Col>
              <TokenSymbol />
            </Col>

            <Col xs />
            <Col>
              <TokenAmount />
            </Col>
          </Row>
        </Pane>
        <Chart />
      </Card>
    </Context.Provider>
  );
}

function TokenName(): JSX.Element {
  return (
    <Heading size={600} fontWeight={`bolder`} opacity={0.8}>
      Digital Cash Euro
    </Heading>
  );
}

function WatchTokenButton(props: EnhancerProps): JSX.Element {
  const [favorite, setFavorite] = useState(true);
  return (
    <IconButton
      {...props}
      appearance="minimal"
      borderRadius={20}
      size="large"
      onClick={() => setFavorite(!favorite)}
      icon={
        favorite ? (
          <StarIcon color="warning" />
        ) : (
          <StarEmptyIcon color="warning" />
        )
      }
    />
  );
}

function TokenLogo(): JSX.Element {
  const address = useContext(Context);

  return (
    <Avatar
      src={`https://picsum.photos/seed/${address}/40/40`}
      name="Alan Turing"
      size={40}
    />
  );
}

function TokenSymbol(): JSX.Element {
  return (
    <Heading size={600} marginLeft={minorScale(3)} opacity={0.4}>
      DCEUR
    </Heading>
  );
}

function TokenAmount(): JSX.Element {
  return (
    <Heading size={600} fontWeight={`bolder`} opacity={0.8}>
      500,00 DCEUR
    </Heading>
  );
}
